import React, {useState} from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom"
import Routes from '../src/Routes/Index'

function App(){
	return(
		<Router>
			<Switch>
				<Route path="/main/" component={Routes} />
			</Switch>
		</Router>
	)
}

export default App;